<template>
  <div class="open-acc">
    <div>
      <el-steps :active="active" finish-status="success" align-center>
        <el-step title="填写银行卡信息"></el-step>
        <el-step title="确认开户"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <el-row class="list">
        <el-col :span="24">
          <div>
            账户类型：<el-select :disabled="true"  style="width: 30%" v-model="value" placeholder="请选择">
            <el-option
              :key="1"
              :label="'对公'"
              :value="'PU'">
            </el-option>
            <el-option
              :key="2"
              :label="'对私'"
              :value="'PR'">
            </el-option>
          </el-select>
          </div>
          <div>
            银行卡号码：<el-input :disabled="true" style="width: 30%" size="medium" v-model="input1" placeholder="请输入内容"></el-input>
          </div>
          <div>
            开户行：<el-input :disabled="true" style="width: 30%" size="medium" v-model="input2" placeholder="请输入内容"></el-input>
          </div>
          <div>
            联系人手机号：<el-input :disabled="active === 2" style="width: 30%" size="medium" v-model="input3" placeholder="请输入内容"></el-input>
          </div>
        </el-col>
        <el-col :span="24" v-if="value === 2">
          <div>
            开户人姓名：<el-input :disabled="true" style="width: 30%" size="medium" v-model="input4" placeholder="请输入内容"></el-input>
          </div>
          <div>
            手机号：<el-input :disabled="true" style="width: 30%" size="medium" v-model="input5" placeholder="请输入内容"></el-input>
          </div>
        </el-col>
      </el-row>
      <el-table
        v-if="value === 1"
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
          prop="mingcheng"
          label="企业名称" show-overflow-tooltip
          align="center">
        </el-table-column>
        <el-table-column
          prop="daima"
          label="组织机构代码" show-overflow-tooltip
          align="center">
        </el-table-column>
        <el-table-column
          prop="bianhao"
          label="营业执照编号"
          align="center">
        </el-table-column>
        <el-table-column
          prop="dengjihao"
          label="税务登记号"
          align="center">
        </el-table-column>
        <el-table-column
          prop="faren"
          label="法人姓名"
          align="center">
        </el-table-column>
        <el-table-column
          prop="shenfenzheng"
          label="法人身份证号"
          align="center">
        </el-table-column>
      </el-table>
      <el-button type="primary" style="margin-top: 30px;" @click="nowOpenAcc"  v-if="active === 2">立即开户</el-button>
      <el-button style="margin-top: 30px;" @click="next">{{nextText}}</el-button>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import Axios from '@/config/axios'
import APIURL from '@/config/APIURL'
Vue.prototype.$ajax = Axios
export default {
  name: 'open-acc',
  data () {
    return {
      active: 1,
      nextText: '下一步',
      tableData: [{
        mingcheng: '',
        daima: '',
        bianhao: '',
        dengjihao: '',
        faren: '',
        shenfenzheng: ''
      }],
      input1: '',
      input2: '',
      input3: '',
      input4: '',
      input5: '',
      value: '',
      openAccParam: {
        'appId': 'org_crm_merchant',
        'loginPhone': sessionStorage.getItem('phone'),
        'reqTime': this.getNowFormatDate(),
        'token': sessionStorage.getItem('token'),
        'recoCode': sessionStorage.getItem('recoCode'),
        'realLegalPhone': ''
      }
    }
  },
  created () {
    this.AcctInfo = JSON.parse(sessionStorage.getItem('AcctInfo'))
    this.input1 = this.AcctInfo.bankCardNoq
    this.input2 = this.AcctInfo.openAcctBankName
    this.input4 = this.AcctInfo.legalName
    this.input5 = this.AcctInfo.legalCertNo
    this.value = this.AcctInfo.businessType
  },
  watch: {
    input3: function (newValue, oldValue) {
      this.openAccParam.realLegalPhone = newValue
    }
  },
  methods: {
    next () {
      if (/^1[0-9]{10}$/.test(this.input3)) {
        this.active++
      } else {
        alert('请输入合法的手机号')
      }
      if (this.active === 2) {
        this.nextText = '上一步'
      } else if (this.active > 2) {
        this.active = 1
        this.nextText = '下一步'
      }
    },
    nowOpenAcc () {
      const merchantOpenDepositAcctUrl = APIURL.merchantOpenDepositAcct
      console.log(this.openAccParam)
      this.$ajax.post(merchantOpenDepositAcctUrl, this.openAccParam).then((res) => {
        if (res.data.code === 0) {
          location.href = res.data.data.jumpUrl
        } else {
          alert(res.data.msg)
        }
      })
    },
    getNowFormatDate () {
      let date = new Date()
      let seperator1 = '-'
      let seperator2 = ':'
      let month = date.getMonth() + 1
      let strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate + ' ' + date.getHours() + seperator2 + date.getMinutes() + seperator2 + date.getSeconds()
      return currentdate
    }
  },
  computed: {
  },
  components: {
  }
}
</script>
<style lang="scss" scoped>
  .open-acc{
    div{
      line-height: 1.2;
      text-align: right;
    }
    .el-steps{
      padding: 20px 0;
    }
    .list{
      div{
        width: 80%;
        margin-top: 5px;
      }
    }
    .el-table{
      margin-top: 30px;
    }
    .el-button{
      display: block;
      margin: 0 auto;
    }
  }
</style>



